import {useEffect, useRef, useState} from "react";
import {loadTossPayments, ANONYMOUS} from "@tosspayments/tosspayments-sdk";
import './Payment.css';

const generateRandomString = () => window.btoa(Math.random()).slice(0, 20);
const clientKey = "test_gck_docs_Ovk5rk1EwkEbP0W43n07xlzm";

export function CheckoutPage() {
    const [ready, setReady] = useState(false);
    const [widgets, setWidgets] = useState(null);
    const [amount, setAmount] = useState({
        currency: "KRW",
        value: 50_000,
    });

    useEffect(() => {
        async function fetchPaymentWidgets() {
            const tossPayments = await loadTossPayments(clientKey);
            const widgets = tossPayments.widgets({customerKey: ANONYMOUS});
            setWidgets(widgets);
        }

        fetchPaymentWidgets();
    }, [clientKey]);

    useEffect(() => {
        async function renderPaymentWidgets() {
            if (widgets == null) {
                return;
            }
            /**
             *     .
             * renderPaymentMethods, renderAgreement, requestPayment    .
             * @docs https://docs.tosspayments.com/sdk/v2/js#widgetssetamount
             */
            await widgets.setAmount(amount);

            await Promise.all([
                /**
                 *  .
                 * @docs https://docs.tosspayments.com/sdk/v2/js#widgetsrenderpaymentmethods
                 */
                widgets.renderPaymentMethods({
                    selector: "#payment-method",
                    //    UI variantKey
                    //       UI     .
                    // @docs https://docs.tosspayments.com/guides/v2/payment-widget/admin#--ui-
                    variantKey: "DEFAULT",
                }),
                /**
                 *  .
                 * @docs https://docs.tosspayments.com/reference/widget-sdk#renderagreement-
                 */
                widgets.renderAgreement({
                    selector: "#agreement",
                    variantKey: "AGREEMENT",
                }),
            ]);

            setReady(true);
        }

        renderPaymentWidgets();
    }, [widgets]);


    return (
        <div id="tosswidget">
            <div id="toss-wrapper" className="wrapper w-100">
                <div className="max-w-540 w-100">
                    <div id="payment-method" className="w-100"/>
                    <div id="agreement" className="w-100"/>
                    <div className="btn-wrapper w-100">
                        <button id="toss-btn"
                                className="toss-btn primary w-100"
                                onClick={async () => {
                                    try {
                                        /**
                                         *  
                                         *    orderId, amount  .
                                         *         .
                                         * @docs https://docs.tosspayments.com/sdk/v2/js#widgetsrequestpayment
                                         */
                                        await widgets?.requestPayment({
                                            orderId: generateRandomString(),
                                            orderName: "   2",
                                            customerName: "",
                                            customerEmail: "kimchunbae@natrualstate.com",
                                            successUrl: window.location.origin + "/sandbox/success" + window.location.search,
                                            failUrl: window.location.origin + "/sandbox/fail" + window.location.search
                                        });
                                    } catch (error) {
                                        console.log(error)
                                    }
                                }}
                        >
                            
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}