import React, { useState, useEffect } from "react";
import DaumPostcode from "react-daum-postcode"; // DaumPostcode 
import "./DeliveryMod.css"; // CSS  import

function DeliveryMod({ currentAddress, onSave, onClose }) {
  const [username, setUsername] = useState(currentAddress?.deliveryUsername || ""); //  
  const [phone, setPhone] = useState(currentAddress?.deliveryPhone || ""); //  
  const [address, setAddress] = useState(currentAddress?.deliveryAddress || ""); //  
  const [zip, setZip] = useState(currentAddress?.deliveryZip || ""); //  
  const [detailAddress, setDetailAddress] = useState(currentAddress?.deliveryDetailAddress || ""); //  
  const [isAddressModalOpen, setIsAddressModalOpen] = useState(false); //   

  // DaumPostcode  
  const handleCompleteAddress = (data) => {
    const fullAddress = data.roadAddress || data.jibunAddress;
    setAddress(fullAddress);
    setZip(data.zonecode);
    setIsAddressModalOpen(false);
  };

  //   
  const handlePhoneChange = (e) => {
    const value = e.target.value.replace(/[^0-9]/g, "");
    if (value.length <= 11) {
      setPhone(value);
    }
  };

  //    
  const handleSave = () => {
    if (!username || !phone || !address || !zip || !detailAddress) {
      alert("   .");
      return;
    }

    const updatedAddress = {
      deliveryNumber: currentAddress?.deliveryNumber, //    
      deliveryUsername: username,
      deliveryPhone: phone,
      deliveryAddress: address,
      deliveryZip: zip,
      deliveryDetailAddress: detailAddress,
    };

    onSave(updatedAddress); //     
  };

  return (
      <div className="modal-overlay">
        <div className="modal-content">
          <h2 className="modal-title"> </h2>

          {/*  */}
          <div className="form-group">
            <label></label>
            <input
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                className="form-input"
            />
          </div>

          {/*  */}
          <div className="form-group">
            <label></label>
            <input
                type="text"
                value={phone}
                onChange={handlePhoneChange}
                className="form-input"
                placeholder="  (11)"
            />
          </div>

          {/*  */}
          <div className="form-group">
            <label></label>
            <div className="address-wrapper">
              <input
                  type="text"
                  value={address}
                  readOnly
                  className="form-input"
              />
              <button
                  onClick={() => setIsAddressModalOpen(true)}
                  className="btn-address-search"
              >
                
              </button>
            </div>
          </div>

          {/*  */}
          <div className="form-group">
            <label></label>
            <input type="text" value={zip} readOnly className="form-input" />
          </div>

          {/*  */}
          <div className="form-group">
            <label></label>
            <input
                type="text"
                value={detailAddress}
                onChange={(e) => setDetailAddress(e.target.value)}
                className="form-input"
                placeholder=" "
            />
          </div>

          {/*     */}
          <div className="button-group">
            <button onClick={onClose} className="btn-cancel">
              
            </button>
            <button onClick={handleSave} className="btn-submit">
              
            </button>
          </div>

          {/* DaumPostcode  */}
          {isAddressModalOpen && (
              <div className="modal-overlay">
                <div className="address-modal">
                  <DaumPostcode onComplete={handleCompleteAddress} />
                  <button
                      onClick={() => setIsAddressModalOpen(false)}
                      className="btn-close-modal"
                  >
                    
                  </button>
                </div>
              </div>
          )}
        </div>
      </div>
  );
}

export default DeliveryMod;
