import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Form, Button, Row } from "react-bootstrap";
import "./Login.css";

function Login({ onLogin }) {
    const [userId, setUserId] = useState("");
    const [password, setPassword] = useState("");
    const [userType, setUserType] = useState("user"); //  : user  store
    const [errorMessage, setErrorMessage] = useState("");
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();

        const data =
            userType === "user"
                ? { userId, password } //   
                : { storeId: userId, password }; //  

        const apiEndpoint =
            userType === "user"
                ? "http://localhost:18080/api/login"
                : "http://localhost:18080/api/store/login";

        try {
            const response = await fetch(apiEndpoint, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(data),
                credentials: "include",
            });

            const result = await response.json(); // JSON   

            if (response.ok) {
                onLogin(result); // App.js   
                alert(result.message); //   
                navigate("/"); //   
            } else {
                setErrorMessage(result.error); //   
            }
        } catch (error) {
            console.error(" :", error);
            setErrorMessage("    .");
        }
    };

    return (
        <div className="login-container">
            <Form noValidate onSubmit={handleSubmit} className="login-form">
                <h5 className="font-label"></h5>
                <hr />
                <Row className="mb-3 user-type-container">
                    <Form.Check
                        type="radio"
                        label=" "
                        name="userType"
                        value="user"
                        checked={userType === "user"}
                        onChange={(e) => setUserType(e.target.value)}
                    />
                    <Form.Check
                        type="radio"
                        label=""
                        name="userType"
                        value="store"
                        checked={userType === "store"}
                        onChange={(e) => setUserType(e.target.value)}
                    />
                </Row>

                <Row className="mb-3">
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label className="font-label"></Form.Label>
                        <Form.Control
                            type="text"
                            placeholder=""
                            value={userId}
                            onChange={(e) => setUserId(e.target.value)}
                            isInvalid={!!errorMessage}
                        />
                    </Form.Group>
                </Row>

                <Row className="mb-3">
                    <Form.Group controlId="formBasicPassword">
                        <Form.Label className="font-label"></Form.Label>
                        <Form.Control
                            type="password"
                            placeholder=""
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            isInvalid={!!errorMessage}
                        />
                        <Form.Control.Feedback type="invalid">
                            {errorMessage}
                        </Form.Control.Feedback>
                    </Form.Group>
                </Row>

                <Button className="font-label btn-submit" type="submit">
                    
                </Button>
            </Form>
        </div>
    );
}

export default Login;