import {useState} from "react";
import {useSearchParams} from "react-router-dom";

export function SuccessPage() {
    const [isConfirmed, setIsConfirmed] = useState(false);
    const [searchParams] = useSearchParams();
    const paymentKey = searchParams.get("paymentKey");
    const orderId = searchParams.get("orderId");
    const amount = searchParams.get("amount");

    async function confirmPayment() {
        // TODO: API   paymentKey, orderId, amount .
        //      API   .
        // https://docs.tosspayments.com/reference#%EA%B2%B0%EC%A0%9C-%EC%8A%B9%EC%9D%B8
        const response = await fetch("/sandbox-dev/api/v1/payments/confirm", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                paymentKey,
                orderId,
                amount
            })
        });

        if (response.ok) {
            setIsConfirmed(true);
        }
    }

    return (
        <div id="tosswidget">
            <div className="wrapper w-100">
                {isConfirmed ? (
                    <div
                        className="flex-column align-center confirm-success w-100 max-w-540"
                        style={{
                            display: "flex"
                        }}
                    >
                        <img
                            src="https://static.toss.im/illusts/check-blue-spot-ending-frame.png"
                            width="120"
                            height="120"
                        />
                        <h2 className="title"> </h2>
                        <div className="response-section w-100">
                            <div className="flex justify-between">
                                <span className="response-label"> </span>
                                <span id="amount" className="response-text">
                {amount}
              </span>
                            </div>
                            <div className="flex justify-between">
                                <span className="response-label"></span>
                                <span id="orderId" className="response-text">
                {orderId}
              </span>
                            </div>
                            <div className="flex justify-between">
                                <span className="response-label">paymentKey</span>
                                <span id="paymentKey" className="response-text">
                {paymentKey}
              </span>
                            </div>
                        </div>

                        <div className="w-100 button-group">

                            <div className="flex" style={{gap: "16px"}}>
                                <a
                                    className="btn w-100"
                                    href="https://developers.tosspayments.com/sandbox"
                                >
                                     
                                </a>
                                <a
                                    className="btn w-100"
                                    href="https://docs.tosspayments.com/guides/v2/payment-widget/integration"
                                    target="_blank"
                                    rel="noopner noreferer"
                                >
                                      
                                </a>
                            </div>
                        </div>
                    </div>
                ) : (
                    <div className="flex-column align-center confirm-loading w-100 max-w-540">
                        <div className="flex-column align-center">
                            <img
                                src="https://static.toss.im/lotties/loading-spot-apng.png"
                                width="120"
                                height="120"
                            />
                            <h2 className="title text-center">  .</h2>
                            <h4 className="text-center description">  .</h4>
                        </div>
                        <div className="w-100">
                            <button className="toss-btn primary w-100" onClick={confirmPayment}>
                                 
                            </button>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );

}