import React from "react";
import styled from "styled-components";

const Container = styled.div`
    max-width: 800px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #eaeaea;
    border-radius: 8px;
    background-color: #f9f9f9;
    font-family: Arial, sans-serif;
    text-align: center;
`;

const Title = styled.h2`
    color: #1b5e20;
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: bold;
`;

const NoticeBox = styled.div`
    border: 1px solid #a5d6a7;
    border-radius: 8px;
    background-color: #e8f5e9;
    padding: 20px;
    margin-bottom: 20px;
    text-align: left;
`;

const NoticeText = styled.p`
    color: #2e7d32;
    font-size: 14px;
    margin: 10px 0;
    line-height: 1.6;
`;

const DeleteButton = styled.button`
    background-color: #d32f2f;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;

    &:hover {
        background-color: #b71c1c;
    }
`;

function DeleteAccount() {
    const handleDeleteAccount = async () => {
        const confirmDelete = window.confirm(" ?");
        if (confirmDelete) {
            try {
                const response = await fetch("http://localhost:18080/api/login/delete", {
                    method: "DELETE",
                    credentials: "include", //   
                });

                if (response.ok) {
                    alert(" .");
                    //     
                    window.location.href = "/login";
                } else {
                    alert(" .");
                }
            } catch (error) {
                console.error("  :", error);
                alert("   .");
            }
        }
    };

    return (
        <Container>
            <Title>   ,    .</Title>
            <NoticeBox>
                <NoticeText>             .</NoticeText>
                <NoticeText>        .</NoticeText>
                <NoticeText>
                           
                </NoticeText>
                <NoticeText>
                             .
                </NoticeText>
            </NoticeBox>
            <DeleteButton onClick={handleDeleteAccount}></DeleteButton>
        </Container>
    );
}

export default DeleteAccount;
