import React, { useState } from "react";
import "./OrderList.css";

const OrderList = () => {
    const [searchQuery, setSearchQuery] = useState("");
    const [orders, setOrders] = useState([
        { id: 1, date: "2025-01-10", productName: "", amount: "1,500,000", status: "" },
        { id: 2, date: "2025-01-08", productName: " ", amount: "250,000", status: "" },
        { id: 3, date: "2025-01-05", productName: " ", amount: "20,000", status: "" },
        { id: 4, date: "2025-01-03", productName: "", amount: "120,000", status: "" },
    ]);

    const [selectedOrder, setSelectedOrder] = useState(null); //   
    const [isModalOpen, setIsModalOpen] = useState(false); //  

    //  
    const filteredOrders = orders.filter((order) =>
        order.productName.includes(searchQuery)
    );

    //  
    const handleOpenModal = (order) => {
        setSelectedOrder(order);
        setIsModalOpen(true);
    };

    //  
    const handleCloseModal = () => {
        setIsModalOpen(false);
        setSelectedOrder(null);
    };

    //  
    const handleReturn = () => {
        alert("  .");
        handleCloseModal();
    };

    return (
        <div className="order-status-container">
            <h1>  .</h1>
            <div className="search-container">
                <input
                    type="text"
                    placeholder=" "
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                />
                <button></button>
            </div>
            <div className="order-list">
                {filteredOrders.map((order) => (
                    <div className="order-item" key={order.id}>
                        <span>
                            - {order.date} | {order.productName} | {order.amount} | {order.status}
                        </span>
                        <button
                            className="detail-button"
                            onClick={() => handleOpenModal(order)}
                        >
                            
                        </button>
                    </div>
                ))}
                {filteredOrders.length === 0 && (
                    <div className="no-orders">  .</div>
                )}
            </div>

            {/*  */}
            {isModalOpen && selectedOrder && (
                <div className="modal-overlay">
                    <div className="modal-content">
                        <h2>  </h2>
                        <p>: {selectedOrder.date}</p>
                        <p>: {selectedOrder.productName}</p>
                        <p>: {selectedOrder.amount}</p>
                        <p>: {selectedOrder.status}</p>
                        <div className="modal-buttons">
                            <button className="return-button" onClick={handleReturn}>
                                
                            </button>
                            <button className="close-button" onClick={handleCloseModal}>
                                
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default OrderList;