import React, { useState, useEffect } from "react";
import DeliveryMod from "./DeliveryMod";
import DeliveryPopup from "./DeliveryPopup";
import "./DeliveryList.css";

//   
const formatPhoneNumber = (phone) => {
  if (!phone) return "";
  const cleaned = phone.replace(/[^0-9]/g, "");
  const match = cleaned.match(/^(\d{3})(\d{4})(\d{4})$/);
  if (match) {
    return `${match[1]}-${match[2]}-${match[3]}`;
  }
  return phone;
};

function DeliveryList() {
  const [addresses, setAddresses] = useState([]); //   
  const [isLoading, setIsLoading] = useState(true); //  
  const [isEditing, setIsEditing] = useState(false); //  
  const [currentAddress, setCurrentAddress] = useState(null); //   
  const [isAdding, setIsAdding] = useState(false); //  

  //   
  useEffect(() => {
    const fetchAddresses = async () => {
      try {
        const response = await fetch("http://localhost:18080/api/delivery/list", {
          credentials: "include", //  
        });
        const data = await response.json();
        console.log("API  :", data); //  
        if (response.ok) {
          // API  key null    
          const sanitizedData = data.map((item) => ({
            deliveryNumber: item.deliveryNumber || "",
            deliveryUsername: item.deliveryUsername || "",
            deliveryPhone: item.deliveryPhone || "",
            deliveryAddress: item.deliveryAddress || "",
            deliveryZip: item.deliveryZip || "",
            deliveryDetailAddress: item.deliveryDetailAddress || "",
          }));
          setAddresses(sanitizedData); //  
        } else {
          console.error("   :", data);
        }
      } catch (error) {
        console.error("    :", error);
      } finally {
        setIsLoading(false); //   
      }
    };
    fetchAddresses();
  }, []);

  //  
  const handleSaveAddress = async (updatedAddress) => {
    try {
      const response = await fetch("http://localhost:18080/api/delivery/update", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify(updatedAddress),
      });
      if (response.ok) {
        const updatedAddresses = addresses.map((item) =>
            item.deliveryNumber === updatedAddress.deliveryNumber
                ? updatedAddress
                : item
        );
        setAddresses(updatedAddresses);
        setIsEditing(false);
      } else {
        console.error("  ");
      }
    } catch (error) {
      console.error("    :", error);
    }
  };

  //  
  const handleDeleteAddress = async (deliveryNumber) => {
    if (window.confirm(" ?")) {
      try {
        const response = await fetch(
            `http://localhost:18080/api/delivery/delete/${deliveryNumber}`,
            {
              method: "DELETE",
              credentials: "include",
            }
        );
        if (response.ok) {
          const updatedAddresses = addresses.filter(
              (item) => item.deliveryNumber !== deliveryNumber
          );
          setAddresses(updatedAddresses);
          alert(".");
        } else {
          console.error("  ");
          alert(" .");
        }
      } catch (error) {
        console.error("    :", error);
        alert("    .");
      }
    }
  };

  //   
  const handleEditAddress = (address) => {
    setIsEditing(true);
    setCurrentAddress(address);
  };

  //   
  if (isLoading) {
    return <div className="loading"> ...</div>;
  }

  return (
      <div className="delivery-container">
        <h1 className="delivery-manage"> </h1>
        <table className="delivery-table">
          <thead className="delivery-thead">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th>/</th>
          </tr>
          </thead>
          <tbody>
          {addresses.map((item, index) => (
              <tr key={item.deliveryNumber}>
                <td>{index + 1}</td>
                <td>{item.deliveryUsername}</td>
                <td>{item.deliveryAddress}</td>
                <td>{item.deliveryDetailAddress}</td>
                <td>{item.deliveryZip}</td>
                <td>{formatPhoneNumber(item.deliveryPhone)}</td>
                <td>
                  <button
                      className="delete"
                      onClick={() => handleDeleteAddress(item.deliveryNumber)}
                  >
                    
                  </button>
                  <button
                      className="edit"
                      onClick={() => handleEditAddress(item)}
                  >
                    
                  </button>
                </td>
              </tr>
          ))}
          </tbody>
        </table>
        <div className="add-container">
          <button className="add" onClick={() => setIsAdding(true)}>
             
          </button>
        </div>
        {isEditing && (
            <DeliveryMod
                currentAddress={currentAddress}
                onSave={handleSaveAddress}
                onClose={() => setIsEditing(false)}
            />
        )}
        {isAdding && (
            <DeliveryPopup
                onAdd={(newAddress) => {
                  setAddresses((prev) => [...prev, newAddress]);
                  setIsAdding(false);
                }}
                onClose={() => setIsAdding(false)}
            />
        )}
      </div>
  );
}

export default DeliveryList;
