import React, { useState } from "react";
import "./RegisteredProduct.css";
import sweetPotatoImage from '../../images/.png';
import strawBerryImage from '../../images/.jpg';
import babyPumpkinImage from '../../images/.jpg'

function RegisteredProduct() {
  const [products, setProducts] = useState([
    {
      id: 1,
      date: "24-12-19 21:52",
      productNumber: "P12345",
      productName: " ",
      image: sweetPotatoImage,
      productPrice: 30000,
      totalPrice: 30000,
    },
    {
      id: 2,
      date: "24-12-20 23:12",
      productNumber: "P12346",
      productName: " ",
      image: strawBerryImage,
      productPrice: 20000,
      totalPrice: 60000,
    },
    {
      id: 3,
      date: "24-12-25 12:25",
      productNumber: "P12347",
      productName: " ",
      image: babyPumpkinImage,
      productPrice: 5000,
      totalPrice: 10000,
    },
  ]);

  return (
    <div className="registered-product-container">
      <h1 className="registered-product-title">  </h1>

      <table className="registered-product-table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th> </th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {products.map((product) => (
            <tr key={product.id}>
              <td>{product.id}</td>
              <td>{product.date}</td>
              <td>{product.productNumber}</td>
              <td>
                <img src={product.image} alt=" " />
                <br />
                {product.productName}
              </td>
              <td>{product.productPrice.toLocaleString()}</td>
              <td>{product.totalPrice.toLocaleString()}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default RegisteredProduct;