import React, { useState } from "react";
import "./ProductDetail.css"; //  CSS  import
import TangerineImage from '../../images/.png';
import Thumbnail1 from '../../images/1.jpg';
import TangerineDescImage from '../../images/1.jpg'

function ProductDetail() {
  const [selectedOption, setSelectedOption] = useState(""); //   
  const [additionalOption, setAdditionalOption] = useState(""); //   
  const [quantity, setQuantity] = useState(1); //  
  const [price, setPrice] = useState(17900); //  

  const handleOptionChange = (e) => {
    setSelectedOption(e.target.value);
  };

  const handleAdditionalOptionChange = (e) => {
    setAdditionalOption(e.target.value);
  };

  const handleQuantityChange = (type) => {
    if (type === "increase") {
      setQuantity((prev) => prev + 1);
    } else if (type === "decrease" && quantity > 1) {
      setQuantity((prev) => prev - 1);
    }
  };

  return (
    <div>
      {/*    */}
      <div className="detail-container">
        {/*    */}
        <div className="detail-image-section">
          <img
            src={TangerineImage}
            alt="Product"
            className="detail-main-image"
          />
        </div>

        {/*     */}
        <div className="detail-detail-section">
          <h2 className="detail-title">[  ]  1</h2>
          <p className="detail-brand"> | </p>
          <p className="detail-price">17,900</p>

          {/*   */}
          <div className="detail-benefits">
            <p>:  10%  ()</p>
            <p>:  |     </p>
          </div>

          {/*   */}
          <div className="detail-option">
            <label className="detail-label"></label>
            <select
              value={selectedOption}
              onChange={handleOptionChange}
              className="detail-select"
            >
              <option value=""> </option>
              <option value="option1"> 1</option>
              <option value="option2"> 2</option>
            </select>
          </div>

          {/*    */}
          <div className="detail-additional-option">
            <label className="detail-label"> ()</label>
            <select
              value={additionalOption}
              onChange={handleAdditionalOptionChange}
              className="detail-select"
            >
              <option value=""> </option>
              <option value="additional1"> 1</option>
              <option value="additional2"> 2</option>
            </select>
          </div>

          {/*   */}
          <div className="detail-quantity-control">
            <button
              onClick={() => handleQuantityChange("decrease")}
              className="detail-quantity-button"
            >
              -
            </button>
            <span className="detail-quantity-display">{quantity}</span>
            <button
              onClick={() => handleQuantityChange("increase")}
              className="detail-quantity-button"
            >
              +
            </button>
          </div>

          {/*   */}
          <div className="detail-total-price">
             : {(price * quantity).toLocaleString()}
          </div>

          {/*  */}
          <div className="detail-action-buttons">
            <button className="detail-cart-button"></button>
            <button className="detail-buy-now-button"></button>
          </div>
        </div>
      </div>

      {/*    */}
      <div className="detail-scroll-section">
        <h3 className="detail-section-title"> </h3>
        <p> : , </p>

        <h3 className="detail-section-title">   !</h3>
        <ul>
          <li>    ,   1-3  !</li>
          <li>,              
              .</li>
        </ul>
        <h3 className="detail-section-title"> </h3>
        <img
          src={TangerineDescImage}
          alt="Additional Info"
          className="detail-additional-image"
        />
        <ul>
          <li>      !
          <br/>
                    !
          <br/>
                  ,     !
          <br/>
              .
          </li>
        </ul>
      </div>
    </div>
  );
}

export default ProductDetail;
