import React, { useState, useEffect } from "react";
import "./AdminOrder.css";

function AdminOrder() {
  const [orders, setOrders] = useState([
    {
      id: 1,
      date: "24-12-19 21:52",
      orderNumber: "234141",
      product: " ",
      image: "https://via.placeholder.com/50",
      quantity: 1,
      productPrice: 30000,
      status: "",
      seller: "",
      buyer: "",
      totalPrice: 30000,
      paymentMethod: "",
    },
    {
      id: 2,
      date: "24-12-23 21:12",
      orderNumber: "234142",
      product: " ",
      image: "https://via.placeholder.com/50",
      quantity: 3,
      productPrice: 20000,
      status: "",
      seller: "",
      buyer: "",
      totalPrice: 60000,
      paymentMethod: " ",
    },
    {
      id: 3,
      date: "24-12-25 12:25",
      orderNumber: "234143",
      product: " ",
      image: "https://via.placeholder.com/50",
      quantity: 2,
      productPrice: 50000,
      status: "",
      seller: "",
      buyer: "",
      totalPrice: 10000,
      paymentMethod: "",
    },
  ]);

  const [totalAmount, setTotalAmount] = useState(0);
  const [searchTerm, setSearchTerm] = useState(""); //   
  const [filteredOrders, setFilteredOrders] = useState(orders); //   

  useEffect(() => {
    //   
    const total = orders.reduce((sum, order) => sum + order.totalPrice, 0);
    setTotalAmount(total);
  }, [orders]);

  useEffect(() => {
    //   
    if (searchTerm === "") {
      setFilteredOrders(orders); //     
    } else {
      const filtered = orders.filter((order) =>
        order.product.toLowerCase().includes(searchTerm.toLowerCase()) ||
        order.buyer.toLowerCase().includes(searchTerm.toLowerCase()) ||
        order.seller.toLowerCase().includes(searchTerm.toLowerCase())
      );
      setFilteredOrders(filtered);
    }
  }, [searchTerm, orders]);

  return (
    <div className="admin-order-container">
      <h1 className="admin-order-title">  </h1>

      {/*     */}
      <div className="admin-order-header">
        <div className="admin-order-total">
          <span>  :</span> <strong>{totalAmount.toLocaleString()}</strong>
        </div>
        <div className="admin-order-search">
          <input
            type="text"
            placeholder=" "
            className="search-input"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)} //  
          />
          <button className="search-button"></button>
        </div>
      </div>

      {/*   */}
      <table className="admin-order-table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {filteredOrders.map((order) => (
            <tr key={order.id}>
              <td>{order.id}</td>
              <td>{order.date}</td>
              <td>{order.orderNumber}</td>
              <td>
                <img src={order.image}/>
                <br />
                {order.product}
              </td>
              <td>{order.quantity}</td>
              <td>{order.productPrice.toLocaleString()}</td>
              <td>{order.status}</td>
              <td>{order.seller}</td>
              <td>{order.buyer}</td>
              <td>{order.totalPrice.toLocaleString()}</td>
              <td>{order.paymentMethod}</td>
            </tr>
          ))}
        </tbody>
      </table>

      {/*  */}
      <div className="pagination">
        <button className="pagination-button"></button>
        {[1, 2, 3, 4, 5].map((page) => (
          <button key={page} className="pagination-button">
            {page}
          </button>
        ))}
        <button className="pagination-button"></button>
      </div>
    </div>
  );
}

export default AdminOrder;